.header{
    background-color: $fondoHero-footer;
    padding-bottom: calc($separacion * 2.5);
}
.hero{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
    @include telefono{
        display: flex;
        flex-direction: column-reverse;
    }
    .hero-info{
        display: flex;
        flex-direction: column;
        @include telefono{
            margin-top: calc($separacion / 2);
        }
    }
    .hero-title{
        font-size: 5.5rem;
        line-height: 6.5rem;
        @include telefono{
            font-size: 3.5rem;
            line-height: 4rem;
        }
    }
    .hero-text{
        @include telefono{
            font-size: 1.8rem;
            margin-top: calc($separacion / 3);
        }
    }
    .hero-img{
        .hero__img{
            width: 100%;
            height: 100%;
        }
    }
}
.rate{
    display: flex;
    align-items: center;
    column-gap: calc($separacion / 4);
    @include telefono{
        display: none;
    }
    &:last-of-type{
        color: $negro;
    }
    .stars{
        display: flex;
        column-gap: 0.5rem;
        align-items: center;
        .star{
            @include img('../icons/star.svg');
            width: 1.5rem;
            height: 1.5rem;
        }
    }
}
.trial{
    display: flex;
    align-items: center;
    column-gap: calc($separacion / 1.5);
    margin: calc($separacion / 2.5) 0;
    @include telefono{
        flex-direction: column;
        row-gap: calc($separacion / 2);
    }
    .trial-btn{
        @include btn;
        @include telefono{
            width: 100%;
            text-align: center;
        }
    }
    .examples-btn{
        color: $colorPrincipal;
        font-weight: $bold;
        border-bottom: 3px solid $colorPrincipal;
    }
}
.benefits{
    display: flex;
    column-gap: calc($separacion / 2);
    align-items: center;
    @include telefono{
        flex-direction: column;
        row-gap: 1.5rem;
        margin-top: 1.5rem;
        align-items: flex-start;
    }
    .benefit{
        display: flex;
        align-items: center;
        column-gap: 1rem;
        .benefit-text{
            @include telefono{
                font-size: 1.8rem;
            }
        }
        .check{
            @include img('../icons/Checkmark.svg');
            height: 2.3rem;
            width: 2.3rem;
        }
    }
}